        <form style="display: none;" action="/j/people/141777985/edit_intro" name="edit_intro" mothod="post">
            <textarea name="intro" style="overflow: hidden;">我有大文豪芥川龙之介那么丧，
又像大文豪太宰治那么悲观，
还比大诗人海子更加颓废，
可我多希望能像大文豪萧伯纳那么幽默，
能像大作家海明威那么硬汉，
更希望能像大哲学家维特根斯坦他们家那么富有！


至今仍然不喜见人
见过的人越多越喜欢猫
伤痕所致，喜欢治愈系的动画或电影

不懂文学，只喜欢凑个热闹
不懂电影，只喜欢看个大概
不懂音乐，只弹过几年吉他，正在学习用cubase
编曲
不懂围棋，至今未搞懂大雪崩定式和妖刀定式
不懂生活，所以过得浑浑噩噩
不懂爱情，所以是个恋爱绝缘体
不懂人生，所以悲欣交集……

---------------------------------------------------------------
2016,2017

出门的时候
想把每一步都踩在落叶上
以为这样
这一生的路途
就都有了去向
脚步殷实，坦然，漫漫……

并不是每一步都会走偏在旅途
但总有时候
会有走偏的必要

在一片落叶的黄昏中佝偻地温和着
夕阳和我
不离不弃

-----------------------------------------------------------------

2018

一月，我从梦中惊醒，一场大雨让我想起了你。

二月，从回头到遗憾，只有一张地图的距离。  

三月，狮子在咆哮，仿佛远方在我跟前。

四月，四月是关于青春的月份，而我从它眼中看到了残酷。

五月，那是一段尘封的记忆，是回忆里的空白。

六月， 一个人想要变得纯粹，需要许多条件。需要世界倒立，时光倒流，命运把镜子打破，还要从背影里扯出荒原 。

七月，我开始了远行，从西到东。

八月，我在水杯中安坐了一整个夏天。

九月，九月是目击众神死亡的草原，九月的第一天是倒霉的日子。

十月，我思索着，煎熬着，等待着命运之神的眷顾。

十一月，十一月的北风呼啸，我无所适从。

十二月，这一年的愿望尚未实现，已不可能。

---------------------------------------------------------------
2019


一月，用来缅怀的时间总是很短暂，
而虚度的时间总是很漫长。而虚度光阴的人
一旦遗忘，虚度之物转瞬即逝。
深夜听雨，从生命的钝感中清醒。
才知痛感是一种深刻的生命体验。

二月，时间是神奇的东西。
每时过境迁之后，才会珍惜。
一想到生命曾无限接近于星空，
就稍微冲淡了我人性之中的狭隘。
若人生没有对错可言，即使它的舞步
一塌糊涂，我，可敢走下去？

三月是我的脚印，而我是这孤独世界
唯一的幽灵，总是习惯于游荡于旷野。

四月的羔羊受了剥削

五月的羔羊砍下了牧羊人的头颅

六月我苟延残喘，我是夏天里的
最后一个孤魂，是哭泣的孤魂
最后一个念念不忘的残念。

七月我遗忘了七月的存在，
在懒散了猫咪的暴雨中回头，
家里蹲到七月的天荒地老，
值得怀念的生命与满怀遗憾的生活。

八月我来到了遥远的重庆，
猜想，在车窗外驻足的行人
血液中是否有山脉起伏。

九月我秉怀这失落与籍籍无名，
把愿望打散在这山城的各个角落。

十月，他们都被染成了红色，
唯独我还呆在灰色的角落里，
诅咒着时光这个大骗子 。

十一月，许多往事在月光之下摇晃。
它的阴影，是苦的。

十二月，吾生也有涯，
吾生即断崖。真是残念呢！

----------------------------------------------------------------
2020

一月我离开重庆，
坐在空空的鼓中回到我空空的家，
等待一场空空的降雪淹没空空的我。

二月的湖面，尽是腐鱼与枯骨。
来自世界的苦难那么深重。我振臂疾呼，
却又自甘堕落。话语扼杀于腹部，
无处安放的呐喊与心中汹涌的火。

三月，我洗漱马匹，尘埃
从体内取出堆积一半的雪
存入半截木头
安逸，慢慢，渐渐，缓缓……
却独自，泣不成声

四月
废物观看我，如镜中影
四月是谁的四月
令和未满，平成不再

五月
把夏天立起来，放置在远处
让它在风中飘纵生存的意志
独自领悟大气，雷鸣与偏爱
饱食一份午餐量的暴雨
想象众生发芽长草，自建囚牢
苦而无味。自是不敢言语
</textarea><br>
